    export default {
        data() {
            return {
                SessionKey: '',
                OpenId: '',
                nickName: null,
                avatarUrl: null,
                isCanUse: uni.getStorageSync('isCanUse')||true//默认为true
            };
        },
        methods: {
            //第一授权获取用户信息===》按钮触发
            wxGetUserInfo() {
                let _this = this;
                uni.getUserInfo({
                    provider: 'weixin',
                    success: function(infoRes) {
						console.log(infoRes);
                        let nickName = infoRes.userInfo.nickName; //昵称
                        let avatarUrl = infoRes.userInfo.avatarUrl; //头像
						let gender = infoRes.gender //性别 未知：1、男 2、女
						let province = infoRes.province
						let city = infoRes.city
						let country = infoRes.country
                        try {
                            uni.setStorageSync('isCanUse', false);//记录是否第一次授权  false:表示不是第一次授权
                            _this.updateUserInfo();
                        } catch (e) {}
                    },
                    fail(res) {}
                });
            },

　　　　　　//登录
                login() {
                let _this = this;
                uni.showLoading({
                    title: '登录中...'
                });
             
               // 1.wx获取登录用户code
                uni.login({
                    provider: 'weixin',
                    success: function(loginRes) {
                        let code = loginRes.code;
                        if (!_this.isCanUse) {
                            //非第一次授权获取用户信息
                            uni.getUserInfo({
                                provider: 'weixin',
                                success: function(infoRes) {
 　　　　　　　　　　　　　　　　　　　　　　//获取用户信息后向调用信息更新方法
                                    let nickName = infoRes.userInfo.nickName; //昵称
                                    let avatarUrl = infoRes.userInfo.avatarUrl; //头像
									let gender = infoRes.gender; //性别 未知：1、男 2、女
									let province = infoRes.province;
									let city = infoRes.city;
									let country = infoRes.country;
                                        _this.updateUserInfo();//调用更新信息方法
                                }
                            });
                        }
                        //2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
                        uni.request({
                            url: 'http://192.168.0.101:8081/cf-user/userInfoLogin',//服务器地址
                            data: {
                                code: code,
                            },
                            method: 'post',//获取用户信息
                            header: {
                                'content-type': 'application/json'
                            },
							xhrFields: {//允许跨域
								withCredentials: true
							},
                            success: (res) => {
                                //openId、或SessionKdy存储//隐藏loading
								console.log(res.message)
                                uni.hideLoading();
                            }
                        });
                    },
                });
            },
         //向后台更新信息
            updateUserInfo() {
                let _this = this;
                uni.request({
                    url:'http://192.168.0.101:8081/cf-user/userInfoLogin' ,//服务器端地址 显示501端口错误
                    data: {
                        appKey: this.$store.state.appKey,
                        customerId: _this.customerId,
                        nickName: _this.nickName,
                        headUrl: _this.avatarUrl,
						gender : _this.gender,
						province : _this.province,
						city: _this.city,
						country:_this.country
                    },
                    method: 'POST',
                    header: {
                        'content-type': 'application/json'
                    },
					xhrFields: {//允许跨域
						withCredentials: true
					},
                    success: (res) => {
                        if (res.data.state == "success") {
                            uni.reLaunch({//信息更新成功后跳转到小程序首页
                                url: '../index/index'
                            });
                        }
                    }
                   
                });
            }
        },
        onLoad() {//默认加载
            this.login();
        }
    }
